import { useEffect } from 'react';
import { fabric } from 'fabric';

const addCanvas = (state, id, dispatch) => {
  let canvas = new fabric.Canvas(id, {
    width: state.width * state.zoom,
    height: state.height * state.zoom,
    zoom: state.zoom,
    backgroundColor: 'rgba(255,255,255,1)',
    preserveObjectStacking: true,
    bgImage: null,
    id,
  });
  dispatch({
    type: 'setCanvasState',
    data: {
      ...state,
      backgroundColor: 'rgba(255,255,255,1)',
      backgroundImage: { type: '', src: null },
    },
  });
  return canvas;
};

const useAddCanvas = (
  state,
  dispatch,
  setCanvas,
  setIsCanvasSet,
  setCanvasArr,
  currentCanvas,
  setFabricCanvasArr,
  —ÅurrentCanvasId,
  shouldChangeCanvas,
  setShouldChangecanvas
) => {
  useEffect(() => {
    if (currentCanvas && shouldChangeCanvas) {
      setShouldChangecanvas(false);
      let canvas = addCanvas(state.canvasState, —ÅurrentCanvasId, dispatch);
      setCanvas(canvas);
      setFabricCanvasArr((prevState) => [...prevState, canvas]);
      setIsCanvasSet(true);
    }
  }, [
    dispatch,
    shouldChangeCanvas,
    setShouldChangecanvas,
    —ÅurrentCanvasId,
    setCanvas,
    setIsCanvasSet,
    state.canvasState,
    setCanvasArr,
    currentCanvas,
    setFabricCanvasArr,
  ]);
};

export default useAddCanvas;
